.form {
  width: 510px;
  height: 670px;
  padding: 56px;
}

.form__title {
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  font-weight: 600;
  margin-bottom: 32px;
}

.form__fields {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
}

.form__field {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  margin-bottom: 32px;
  position: relative;
}

.form__field.success input {
  border-color: #2ecc71;
}

.form__field.error input {
  border-color: #e74c3c;
}

.errors {
  color: red;
  position: absolute;
  content: "";
  bottom: -22px;
  left: 0;
}

input.error,
textarea.error {
  border: 1px solid red;
}

.form {
  position: relative;
}

.form::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ccc;
  opacity: 0;
  visibility: hidden;
}

.form.sending::after {
  opacity: 1;
  visibility: visible;
}

@media only screen and (max-width: 576px) {
  .form {
    width: auto;
    max-width: 510px;
    min-width: 300px;
    padding: 24px;
  }
}